import React, { Component, Fragment, Suspense } from 'react';
import { Route, Switch, Redirect } from 'react-router-dom';
import { HashLink } from 'react-router-hash-link';
import { Col, Row, Nav, NavItem, Badge } from 'reactstrap';
import { components_routes as routes } from '../routes';
import { local_urls } from './../urls';
import Icon from '@mdi/react';
import { mdiMenu } from '@mdi/js';
import Collapse from 'reactstrap/lib/Collapse';

export default class Components extends Component {
    constructor(props) {
        super(props);
        this.state = {
            is_menu_open: true
        };
    }

    showMenu = function () {
        this.setState({ is_menu_open: !this.state.is_menu_open });
    }.bind(this)

    render() {
        var side_menu = <Nav vertical>
            <NavItem>
                <HashLink className="nav-link" to={`${local_urls["version_prefix"]}/components/designer`}>Designer</HashLink>
                <Nav vertical>
                    <NavItem>
                        <HashLink className="nav-link" to={`${local_urls["version_prefix"]}/components/designer/icons`}>Icons</HashLink>
                    </NavItem>
                    <NavItem>
                        <HashLink className="nav-link" to={`${local_urls["version_prefix"]}/components/designer#methods`}>Methods</HashLink>
                    </NavItem>
                    <NavItem>
                        <HashLink className="nav-link" to={`${local_urls["version_prefix"]}/components/designer#parameters`}>Parameters</HashLink>
                    </NavItem>
                    <NavItem>
                        <HashLink className="nav-link" to={`${local_urls["version_prefix"]}/components/designer#properties`}>Properties</HashLink>
                    </NavItem>
                    <NavItem>
                        <HashLink className="nav-link" to={`${local_urls["version_prefix"]}/components/designer/translations`}>Translations</HashLink>
                    </NavItem>
                </Nav>
            </NavItem>
            <NavItem>
                <HashLink className="nav-link" to={`${local_urls["version_prefix"]}/components/generator`}>Generator</HashLink>
                <Nav vertical>
                    <NavItem>
                        <HashLink className="nav-link" to={`${local_urls["version_prefix"]}/components/generator#methods`}>Methods</HashLink>
                    </NavItem>
                    <NavItem>
                        <HashLink className="nav-link" to={`${local_urls["version_prefix"]}/components/generator#parameters`}>Parameters</HashLink>
                    </NavItem>
                    <NavItem>
                        <HashLink className="nav-link" to={`${local_urls["version_prefix"]}/components/generator#properties`}>Properties</HashLink>
                    </NavItem>
                </Nav>
            </NavItem>
            <NavItem>
                <HashLink className="nav-link" to={`${local_urls["version_prefix"]}/components/validation-methods`}>Validation methods</HashLink>
            </NavItem>
            <NavItem>
                <HashLink className="nav-link" to={`${local_urls["version_prefix"]}/codesheet`}>Codesheet</HashLink>
            </NavItem>
            <NavItem className="nav-link-title mt-2">
                Tools
            </NavItem>
            <NavItem>
                <HashLink className="nav-link" to={`${local_urls["version_prefix"]}/playground`}>Playground</HashLink>
            </NavItem>
            <NavItem>
                <HashLink className="nav-link" to={`${local_urls["version_prefix"]}/components/designer/translation-generator`}>Translation generator</HashLink>
            </NavItem>
        </Nav>;

        return (
            <Fragment>
                <Row className="pt-3" >
                    <Col sm="12" className="mx-300-alter p-0">
                        <Nav vertical>
                            <NavItem>
                                <HashLink className="nav-link" to={`${local_urls["version_prefix"]}/components/whats-new`}><Badge color="primary" >New</Badge> What's new?</HashLink>
                            </NavItem>
                            <NavItem className="nav-link-title">
                                Components 
                                <button className="navbar-toggler float-right" onClick={this.showMenu}>
                                    <Icon path={mdiMenu} color="#77757a" size={1.1} />
                                </button>
                            </NavItem>
                        </Nav>
                        <Collapse isOpen={this.state.is_menu_open}>
                            {side_menu}
                        </Collapse>
                    </Col>
                    <Col>
                        <Suspense fallback={this.props.page_loading}>
                            <Switch>
                                {routes.map((route, idx) => {
                                    return route.component ? (
                                        <Route
                                            key={idx}
                                            path={`${route.path}`}
                                            exact={route.exact}
                                            name={route.name}
                                            render={props => (
                                                <route.component {...props}
                                                    page_loading={this.props.page_loading}
                                                    loading={this.props.loading}
                                                    processing={this.props.processing}
                                                    onRedirect={this.props.navigate}
                                                />
                                            )} />
                                    ) : (null);
                                })}
                                <Redirect from={`${local_urls["version_prefix"]}/components`} to={`${local_urls["version_prefix"]}/components/generator`} />                                
                                <Redirect to={`${local_urls["version_prefix"]}/components`} />
                            </Switch>
                        </Suspense>
                    </Col>
                    <Col className="mx-300">
                        <Nav vertical>
                            <NavItem>
                                <HashLink className="nav-link" to={`${local_urls["version_prefix"]}/components/whats-new`}><Badge color="primary" >New</Badge> What's new?</HashLink>
                            </NavItem>
                            <NavItem className="nav-link-title">
                                Components
                            </NavItem>
                        </Nav>
                        {side_menu}
                    </Col>
                </Row>
            </Fragment>
        );

    }
}